

































// @ts-ignore
import VueDraggableResizable from 'vue-draggable-resizable';
import {
  defineComponent, computed,
  PropType,
} from '@vue/composition-api';
import useStore from '@/hooks/useStore';
import { INode } from '@/types';

import useDragNode from './hooks/useDragNode';
import PortWrapperComponent from '../Port/Wrapper.vue';

type IFlowchartComponent = ReturnType<typeof defineComponent>;

export default defineComponent({
  name: 'NodeWrapper',

  components: {
    VueDraggableResizable,
    PortWrapperComponent,
  },

  props: {
    node: {
      type: Object as PropType<INode>,
      required: true,
    },

    isSelected: {
      type: Boolean,
      default: false,
    },

    nodeComponent: {
      type: Object as PropType<IFlowchartComponent>,
      required: true,
    },
    portComponent: {
      type: Object as PropType<IFlowchartComponent>,
      required: true,
    },
  },

  setup (props) {
    const store = useStore();
    const node = computed(() => props.node);
    const scale = computed(() => store.state.graph.scale);

    const onNodeClick = () => {
      store.dispatch('selectNode', props.node.id);
    };

    return {
      onNodeClick,
      scale,
      ...useDragNode(store, node),
    };
  },
});
